<template>
	<div class="z-add-img-container">
		<div class="mui-clearfix add-img-wrap" v-show='!options.isCropper'>
			<!--  -->
			<div class="mui-pull-left z-img-item" v-for='(item, ind) in list' :key='ind' :style="'background-image: url('+item.base64+')'">
				<i class="z-delect-btn" @tap='delect(ind)'></i>
			</div>

			<!--  -->
			<div class="mui-pull-left z-add-btn" @tap='addEvent' v-if='list.length < opt.maxCount'>
				<i class="iconfont icon-Plus"></i>
			</div>
		</div>
		
		<cropper
			:options='cropper'
			:class='{"z-cropper": options.isCropper}'
			v-show='options.isCropper'
			@onCropper='onCropper'
			@onCancle='onCancle' />
		<input type="file" class='z-file-input-btn' accept="image/*" />
	</div>
</template>

<script>
import HeadCell from '@/components/Cell/HeadCell.vue'
import Cropper from '@/components/Other/Cropper.vue'
import { selectImg } from '@/common/js/utils'
export default {
	data() {
		return {
			isCropper: false
		}
	},
	props: {
		options: {
			type: Object,
			default: function() {
				return {
					isCropper: false
				}
			}
		},
		list: {
			type: Array,
			default: function() {
				return []
			}
		},
		cropper: {
			type: Object,
			default: function() {
				return {
					src: ''
				}
			}
		}
	},
	computed: {
		opt() {
			let opt = {
				maxCount: 4,			// 
			}
			return mui.extend(true, {}, opt, this.options)
		},
	},
	methods: {
		// 
		async addEvent() {
			try {
				let path = await selectImg()
				this.cropper.src = path
				this.options.isCropper = true
			} catch(e) {
				console.log(e);
			}
		},
		// 
		delect(index) {
			this.list.splice(index, 1)
		},
		// 
		getImages() {
			return this.list
		},
		// 
		onCropper(base64) {
			this.options.isCropper = false
			this.list.push(base64)
		},
		// 
		onCancle(cropper) {
			this.options.isCropper = false
		}
	},
	components: {
		Cropper
	}
}
</script>

<style lang='scss'>
$addBtnSize: calc(25vw - 17.5px);
$deleBtnSize: 18px;
$deleBtnPos: -5px;
html, body, .z-add-img-container {
	height: 100%;
}
.z-add-img-container {
	background-color: #fff;
	padding: 10px 15px;
	.z-img-item,
	.z-add-btn {
		width: $addBtnSize;
		height: $addBtnSize;
		line-height: $addBtnSize;
		margin: 5px;
		text-align: center;
	}
	/**/
	.z-img-item {
		position: relative;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		/**/
		.z-delect-btn {
			position: absolute;
			width: $deleBtnSize;
			height: $deleBtnSize;
			top: $deleBtnPos;
			right: $deleBtnPos;
			background-image: url(data:image/png;base64,);
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center;
		}
	}


	/**/
	.z-add-btn {
		border: 1px dashed #ccc;
		.iconfont {
			font-size: 4rem;
			color: #ccc;
		}
	}
	
	.z-cropper-container {
		display: none;
		/**/
		&.z-cropper {
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100vw;
			height: 100vh;
			z-index: 999;
		}
	}
}
</style>


